import styled, { keyframes } from "styled-components";

const fadein = keyframes`
    from{
        opacity: 0;
     
    }
    to{
        opacity: 1;
    }
    
`;

const WingContainer = styled.div`
  animation: ${fadein} 1s ease-in;

  * {
    animation: ${fadein} 0.3s ease-in;
  }
`;

export { WingContainer };
